* {
  transition: 0.5s; }

body {
  margin: 0;
  font-family: "Montserrat Alternates",sans-serif; }

.main {
  margin: 1rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 4fr 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px; }
  .main .current-weather-section {
    grid-area: 1 / 1 / 2 / 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .main .current-weather-section article {
      text-align: center; }
      .main .current-weather-section article .location-name, .main .current-weather-section article .local-time, .main .current-weather-section article .weather-condition, .main .current-weather-section article .weather-icon, .main .current-weather-section article .weather-temperature, .main .current-weather-section article .wind-speed {
        margin: 0; }
      .main .current-weather-section article .location-name, .main .current-weather-section article .weather-condition {
        font-size: 1.5rem;
        margin-bottom: 1rem; }
      .main .current-weather-section article .local-time {
        font-size: 1rem;
        margin-bottom: 1rem; }
      .main .current-weather-section article .weather-icon {
        margin-bottom: 2rem; }
  .main .daily-forecast-section {
    grid-area: 1 / 2 / 3 / 3; }
    .main .daily-forecast-section .forcast-container .forecast-slot .forecast-date {
      text-align: center; }
    .main .daily-forecast-section .forcast-container .forecast-slot .forecast-schedule-list {
      display: flex;
      flex-flow: row wrap;
      justify-content: center;
      align-items: center; }
      .main .daily-forecast-section .forcast-container .forecast-slot .forecast-schedule-list .forecast-details {
        margin: 0.5rem;
        display: flex;
        flex-direction: column;
        align-items: center; }
      .main .daily-forecast-section .forcast-container .forecast-slot .forecast-schedule-list .forecast-details:hover {
        transform: scale(1.5); }
  .main .app-description {
    grid-area: 2 / 1 / 3 / 2;
    display: flex;
    justify-content: center;
    align-items: center; }

.title {
  text-align: center;
  font-size: 2rem; }

.section {
  margin: 0.5rem;
  padding: 1rem;
  border-radius: 2rem;
  border: #1ac6ff 10px solid;
  box-shadow: #0086b3 0.5rem 0.5rem 0px; }
